import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  data() {
    return {

    };
  },

  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartProductsLoading: true,
    cartProductsLoadingFailed: false,
    orderInfo: null,
    orderInfoLoading: true,
    orderInfoLoadingFailed: false,
  },

  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    changeQuantity(state, { productId, amount }) {
      const item = state.cartProducts.find((item) => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    // addProductToCart(state, { productId, amount }) {
    //   const item = state.cartProducts.find((item) => item.productId === productId);
    //   if (item) {
    //     item.amount += amount;
    //   } else {
    //     state.cartProducts.push({
    //       productId,
    //       amount,
    //     });
    //   }
    // },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((item) => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId);
    },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },

    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
  },

  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductsData
          .find((p) => p.product.id === item.productId);

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
            // Подмешиваем картинку из адреса
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.product.price * item.amount + acc,
        0,
      );
    },
  },

  actions: {
    // loadOrderInfo(context, orderId) {
    //   return axios
    //     .get(`${API_BASE_URL}/api/orders/${orderId}`, {
    //       params: {
    //         userAccessKey: context.state.userAccessKey,
    //       },
    //     })
    //   // если запрос успешен при получении ответа отсюда запускаем мутацию
    //     .then((response) => {
    //       context.commit('updateOrderInfo', response.data);
    //     });
    // },

    loadOrderInfo(context, orderId) {
      this.state.orderInfoLoading = true;
      this.state.orderInfoLoadingFailed = false;
      clearTimeout(this.orderInfoLoadingTimer);

      this.orderInfoLoadingTimer = setTimeout(() =>
      axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
      // если запрос успешен при получении ответа отсюда запускаем мутацию
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        })
        .catch(() => this.state.orderInfoLoadingFailed = true)
        .then((response) => this.state.orderInfoLoading = false), 2000);
    },

    loadCart(context) {
      this.state.cartProductsLoading = true;
      this.state.cartProductsLoadingFailed = false;
      clearTimeout(this.cartProductLoadingTimer);

      this.cartProductLoadingTimer = setTimeout(() => axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch(() => this.state.cartProductsLoadingFailed = true)
        .then(() => this.state.cartProductsLoading = false), 2000);
    },

    addProductToCart(context, { productId, amount }) {
      // return new Promise((resolve) => setTimeout(resolve, 2000)).then(() => {
      return axios
        .post(
          `${API_BASE_URL}/api/baskets/products`,
          {
            productId,
            quantity: amount,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },

    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });
      // if (amount < 1) {
      //   return;
      // }
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },

    deleteCartProduct(context, productId) {
      context.commit('deleteCartProduct', productId);

      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data: {
            productId,
          },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
  },
});
