
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
       {{ countProducts }} товар.
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoriesId" :color-title-id.sync="filterColors" />

      <section class="catalog">
        <div v-if="productsLoading"> Подождите, товары загружаются...</div>
        <div v-if="productsLoadingFailed">
           Произошла ошибка при загрузке товаров
           <button @click.prevent="loadProducts">Попробовать еще раз </button>
        </div>
        <productList :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productPerPage" />

      </section>

    </div>
  </main>
</template>

<script>
// import products from '@/products';- не нужен, если берем с сервера
import productList from '@/components/productList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from "@/config";

/* eslint-disable */
export default {

  components: { productList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoriesId: 0,
      filterColorTitle: 0,
      filterColors: 0,

      page: 1,
      productPerPage: 3,

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {
    // filteredProducts() {
    //   let filteredProducts = products;

    //   if (this.filterPriceFrom > 0) {
    //     filteredProducts = filteredProducts.filter(product => product.price > this.filterPriceFrom);
    //   }

    //   if (this.filterPriceTo > 0) {
    //     filteredProducts = filteredProducts.filter(product => product.price < this.filterPriceTo);
    //   }

    //   if (this.filterCategoriesId) {
    //     filteredProducts = filteredProducts.filter(product => product.categoryId === this.filterCategoriesId);
    //   }

    //   if (this.filterColorTitle) {
    //     filteredProducts = filteredProducts.filter(product => product.titleId === this.filterColorTitle);
    //   }

    //   return filteredProducts;
    // },
    // этот код фильтровал товары локального  списка,
    //  если теперь берем товары с сервера, то его убираем



    products() {
      // let offset = (this.page - 1) * this.productPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productPerPage);
      // этот код берет данные из локальной страницы products

      // return this.productsData.items;
      // а этот берет данные с сервера, но он не выведет данные,
      //тк браузер отрисовывает страницу , не успев получить ответ,
      // поэтому делаем так-
      return this.productsData
        ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url
          }
        }) : [];
      // добавляем метод map, чтобы картинки выводились
    },

    countProducts() {
      // return this.filteredProducts.length;
      // это общее кол-во товаров выводилось из локального списка

      return this.productsData ? this.productsData.pagination.total : 0;
      //     потому что на сервере оно хранится так-
      //  "pagination": {
      //   "page": 1,
      //   "pages": 11,
      //   "total": 21
      // }

    }
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios
          .get( API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productPerPage,
              colorId:this.filterColors,
              categoryId: this.filterCategoriesId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo
            }
          })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);


        // .then(response => {console.log(response);this.productsData=response.data})
      }, 0);

    }
  },

  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoriesId() {
      this.loadProducts();
    },
    filterColorTitle() {
      this.loadProducts();
    },
    filterColors(){
      this.loadProducts();
    }
  },
  // добавляем наблюдатель, смотрим за page, если онa изменилась то вызываем loadProducts

  created() {
    this.loadProducts();
  }

}

</script>

